import React, { useEffect, useState } from "react";
import { createRoot } from "react-dom/client";
import { BrowserRouter, Route, Routes, Link } from "react-router-dom";
import Register from "./Register";
import Login from "./Login"
import AdminDashboard from "./AdminDashboard";
import ManageManufacturers from "./ManageManufacturers";
import ManageProducts from "./ManageProducts";

const App = () => {
  const [manufacturers, setManufacturers] = useState([]);
  const [selectedManufacturer, setSelectedManufacturer] = useState(null);
  const [beers, setBeers] = useState([]);

  const fetchManufacturers = async () => {
    try {
      const response = await fetch("http://localhost:3000/craftbeer/allmanufacturers");
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      const data = await response.json();
      setManufacturers(data);
    } catch (err) {
      console.log(err.message);
    }
  };

  const fetchBeers = async (manufacturerId) => {
    try {
      const response = await fetch(`http://localhost:3000/craftbeer/products/${manufacturerId}`);
      if (!response.ok) {
        throw new Error(`HTTP Error. Status: ${response.status}`);
      }
      const data = await response.json();
      setBeers(data);
    } catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    fetchManufacturers();
  }, []);

  useEffect(() => {
    if (selectedManufacturer) {
      fetchBeers(selectedManufacturer);
    }
  }, [selectedManufacturer]);

  return (
    <div>
      <h1>Welcome to <b>Craft Beer Library</b></h1>
      <p>Manufacturers:</p>
      <ul>
        {manufacturers.map((manufacturer) => (
          <li key={manufacturer._id}>
            <b>{manufacturer.naziv}</b> ({manufacturer.drzava}, {manufacturer.godina_osnivanja})
            <br />
            {manufacturer.opis}
            <br />
            <button onClick={() => setSelectedManufacturer(manufacturer._id)}>View products</button>
          </li>
        ))}
      </ul>
      {selectedManufacturer && (
        <div>
          <h2>Products for {manufacturers.find(m => m._id === selectedManufacturer)?.naziv}</h2>
          <ul>
            {beers.map((beer) => (
              <li key={beer._id}>
                <b>{beer.naziv}</b> - {beer.tip} - {beer.cijena} EUR
                <p>{beer.opis}</p>
                <img src={`/public/images/pivo_bright_arrow.jpg`}></img>
              </li>
            ))}
          </ul>
        </div>
      )}
      <Link to="/user/register">
        <button>Register</button>
      </Link>

      <Link to="/user/login">
        <button>Login</button>
      </Link>
      
    </div>
  );
};

const root = createRoot(document.getElementById("root"));
root.render(
  <BrowserRouter> 
    <Routes>
      <Route path="/" element={<App />} />
      <Route path="/user/register" element={<Register />} />
      <Route path="/user/login" element={<Login />} />
      <Route path="/admin" element={<AdminDashboard />}/>
      <Route path="/admin/manufacturers" element={<ManageManufacturers />}/>
      <Route path="/admin/products" element={<ManageProducts />}/>
    </Routes>
  </BrowserRouter>
);

